.profile__title {
  margin: 0;
  font-size: 42px;
  font-weight: 500;
  line-height: 48px;
  letter-spacing: 0em;
  text-align: left;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
  max-width: 294px;
  
}

@media screen and (max-width: 768px) {
  .profile__title {
    font-size: 27px;
    line-height: 33px;
    max-width: 189px;
    text-align: center;
  }
}